
import { defineComponent } from "vue";
import About from "../components/About.vue";
import PostC from "../components/Post.vue";
import type { Sub, Post } from "../store/modules/types";

export default defineComponent({
  name: "Sub",
    components: {
    About,
    PostC,
  },
  data() {
    return {
      sort: "points",
      order: -1,
    };
  },
  computed: {
    dataError(): string {
      return this.$store.direct.state.DataMod.error;
    },
    sub(): Sub | undefined {
      return this.$store.direct.state.DataMod.subs[0];
    },
    posts(): Post[] {
      return this.$store.direct.state.DataMod.subs[0].posts ?? []
    },
    isAuthenticated(): boolean {
      return this.$store.direct.state.AuthMod.isAuthenticated;
    },
  },
  watch: {
    sort: function() {
      this.$store.direct.dispatch.DataMod.subInit({subName: this.$route.params.sub as string, order: this.order, sortBy: this.sort})
    },
  },
  mounted: async function() {
    await this.$store.direct.dispatch.DataMod.subInit({
      subName: this.$route.params.sub as string,
      order: -1,
      sortBy: "points",
    });

    if (this.dataError) this.$router.push("/NotFound");
    if (this.isAuthenticated)
      this.$store.direct.dispatch.getPoints(
        this.$store.direct.getters.DataMod.targetIDs
      );
  },
});
